import { memo } from "react";
import { useTranslateId } from "../translation-layer"
import { StyledHeading } from "./styles";
import { TextTitleProps } from "./interface";

const Text = ({
  title,
  width,
  padding,
  margin,
  color,
  backGround,
  fontWeight,
  fontSize
}: TextTitleProps) => {
  const translate = useTranslateId()
  return (
    <StyledHeading
      width={width}
      margin={margin}
      padding={padding}
      color={color}
      backGround={backGround}
      fontWeight={fontWeight}
      fontSize={fontSize}
    >
      {translate(title)}
    </StyledHeading>
  );
};

export default memo(Text);
