import { memo } from "react";
import { useUserLanguage } from "../app-config-provider"
import { Container, Title, Message, BackButton } from "./style";

const NotFoundPage = () => {
  const p_language = useUserLanguage()
  console.log(p_language)
  return (
    <Container>
      <Title>404</Title>
      <Message>{p_language === 1 ? "Page not found" : "   "}</Message>
      <BackButton to="/home">Go back to home</BackButton>
    </Container>
  );
};

export default memo(NotFoundPage);
