import styled from "@emotion/styled";
import { primaryColors } from "../constants";
import { ButtonProp } from "./interface";

const { blue, white2, lightBlue } = primaryColors;

export const StyledButton = styled.button<ButtonProp>`
  appearance: button;
  margin: ${({ margin }) => margin};
  height: ${({ height }) => height};
  backface-visibility: hidden;
  padding: ${({ padding }) => padding || "3px"};
  width: ${({ width }) => width};
  background-color: ${({ backGround, loading }) =>
    loading ? "white" : backGround ? backGround : blue};
  border-width: 0;
  border-radius: ${({ borderRadius }) => borderRadius || "5px"};
  font-weight: ${({ fontWeight }) => fontWeight};
  border: ${({ border }) => border || "none"};
  font-size: ${({ fontSize }) => fontSize};
  color: ${({ color, loading }) =>
    loading ? lightBlue : color ? color : white2};
  box-shadow: rgba(50, 50, 93, 0.1) 0 0 0 1px inset,
    rgba(50, 50, 93, 0.1) 0 2px 5px 0, rgba(0, 0, 0, 0.07) 0 1px 1px 0;
  box-sizing: border-box;
  cursor: pointer;
  line-height: 1.15;
  outline: none;
  overflow: hidden;
  position: relative;
  text-align: center;
  text-transform: none;
  transform: translateZ(0);
  transition: all 0.2s, box-shadow 0.08s ease-in;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;

  &:disabled {
    cursor: not-allowed;
  }

  &:focus {
    box-shadow: rgba(50, 50, 93, 0.1) 0 0 0 1px inset,
      rgba(50, 50, 93, 0.2) 0 6px 15px 0, rgba(0, 0, 0, 0.1) 0 2px 2px 0,
      rgba(50, 151, 211, 0.3) 0 0 0 4px;
  }
`;

export const ButtonWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
  gap: 5px;
`;
