import { memo } from "react";
import Text from "./text"
import { primaryColors } from "../constants"
import { PageTitleProps } from "./interface";

const PageTitle = ({
  children,
  width,
  padding,
  margin = "10px 0px 5px 0px",
}: PageTitleProps) => {
  const { primary } = primaryColors
  return (
    <Text
      title={children}
      width={width}
      padding={padding}
      margin={margin}
      color={primary}
      fontSize="20px"
      fontWeight="400"
    />
  );
};

export default memo(PageTitle);
