import { useState, useEffect, useCallback, useRef, useMemo } from "react";
import { API_ID } from "../constants";
import { usePrevious } from "../hooks";
import {
  useSelectAuthorization,
  useUserLanguage,
} from "../app-config-provider";
import { objectIs } from "../helpers";
import { useFetchProp, RecordWithAnyData, fetchFunParamType } from "../types";

const useFetch = ({
  apiId,
  fetchOnFirstRun,
  checkForParams = false,
  params,
  runQueryWhenLanguageChanged,
  onResponse,
  skipQuery,
  noAuthorization,
}: useFetchProp) => {
  const apiLink = process.env.REACT_APP_API_LINK;
  const [data, setData] = useState<RecordWithAnyData>();
  const [loading, setLoading] = useState(false);
  const authorization = useSelectAuthorization();
  const p_language = useUserLanguage();
  const baseNextParams = params || {};
  const previousParams = usePrevious(baseNextParams);
  const queryCalledOnFirstRender = useRef<boolean>(false);
  const canRunQuery = useMemo(
    () => !!(noAuthorization || authorization),
    [authorization, noAuthorization]
  );
  const hasParamsChanged = !objectIs(baseNextParams, previousParams);

  const url = `${apiLink}/${API_ID[apiId]}`;

  const generateBasicRequest = useCallback(
    async ({ params, authorization }: fetchFunParamType) => {
      try {
        setLoading(true);
        // await new Promise((resolve) => setTimeout(resolve, 100));
        if (authorization) {
          const headers = {
            Accept: "application/json",
            "Content-Type": "application/json",
            Authorization: `Token ${authorization}`,
          };
          const response = await fetch(
            `${url}?${new URLSearchParams(params)}`,
            {
              headers,
            }
          );
          if (!response.ok) {
            setLoading(false);
            // If the response status is not ok (e.g., 404 or 500), throw an error
            throw new Error(`HTTP error! Status: ${response.status}`);
          }

          setLoading(false);
          const apiResponse = await response.json();
          setData(apiResponse);
          onResponse && onResponse(apiResponse);
          return apiResponse;
        } else {
          return [];
        }
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    },
    [onResponse, url]
  );

  const resetData = useCallback(() => {
    setData(undefined);
  }, []);

  const runFetch = (e?: RecordWithAnyData) => {
    if (e) {
      !skipQuery &&
        generateBasicRequest({ params: e, authorization: authorization });
    } else {
      !skipQuery &&
        generateBasicRequest({
          params: previousParams,
          authorization: authorization,
        });
    }
  };

  useEffect(() => {
    if (canRunQuery) {
      const canRunQueryForFirstRender =
        !!fetchOnFirstRun && !queryCalledOnFirstRender.current && !!p_language;

      if (canRunQueryForFirstRender || (checkForParams && hasParamsChanged)) {
        queryCalledOnFirstRender.current = canRunQueryForFirstRender;
        const computedParams = {
          p_language,
          ...baseNextParams,
        };
        generateBasicRequest({
          params: computedParams,
          authorization: authorization,
        });
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    authorization,
    baseNextParams,
    canRunQuery,
    checkForParams,
    fetchOnFirstRun,
  ]);

  return {
    data,
    runFetch,
    setData,
    loading,
    resetData,
    previousParams,
  };
};

export default useFetch;
