import { memo, useCallback } from "react";
import { useNavigate } from "react-router-dom";
import { BaseTitle } from "../page-title";
import IconButton from "../icon-button";
import {
  useSelectBuildNumber,
  useSelectUserName,
} from "../app-config-provider";
import Flex from "../flex";
import { useLocalStorage } from "../hooks";
import { StyledHeader, LogoImage, LogoContainer, HeaderTitle } from "./styled";

const Header = () => {
  const { clearAllLocalStorage } = useLocalStorage();
  const navigate = useNavigate();
  const build_number = useSelectBuildNumber();
  const user_name = useSelectUserName();

  const handleSignOut = useCallback(() => {
    clearAllLocalStorage();
    navigate("/");
  }, [clearAllLocalStorage, navigate]);

  return (
    <>
      <StyledHeader>
        <Flex
          width="20%"
          gap="5px"
          justifyContent="center"
          flexDirection="column"
          align="center"
          margin="0 5px"
        >
          <HeaderTitle disableTranslation>test</HeaderTitle>
          <BaseTitle disableTranslation>{build_number}</BaseTitle>
        </Flex>
        <LogoContainer to={`/home`}>
          <LogoImage
            src={`${process.env.REACT_APP_BASE_LINK}app_images/primary_logo`}
            alt="company logo"
          />
        </LogoContainer>
        <Flex
          width="20%"
          gap="15px"
          justifyContent="center"
          align="center"
          margin="0 5px"
        >
          <Flex justifyContent="center" flexDirection="column" align="center">
            <HeaderTitle disableTranslation>{user_name}</HeaderTitle>
          </Flex>
          <IconButton iconName="signOut" onClick={handleSignOut} />
        </Flex>
      </StyledHeader>
    </>
  );
};

export default memo(Header);
