import { useState, useLayoutEffect } from "react";
import { useLocation } from "react-router-dom";
import { useSelectUserName } from "../app-config-provider";
import { useFetch } from "../hooks";
import Store from "./context/context";
import { RecordWithAnyData } from "../types";

const PrivilegeLayer = ({ children }: any) => {
  const [basePage, setBasePage] = useState<RecordWithAnyData>({});
  const { pathname } = useLocation();
  const page_link = pathname.replace("/", "");
  const user_name = useSelectUserName();

  useLayoutEffect(() => {
    setBasePage({});
  }, [page_link]);

  const { page_id } = basePage;

  useFetch({
    apiId: "QUERY_PAGE_PRIVILEGE_TABLE_DATA",
    onResponse: setBasePage,
    fetchOnFirstRun: true,
    checkForParams: true,
    params: {
      page_link,
      user_name,
    },
  });

  const isLogInPage = pathname === "/";

  const shouldRenderApp = page_id || isLogInPage;

  return (
    <>
      {shouldRenderApp ? (
        <Store.Provider value={basePage}>{children}</Store.Provider>
      ) : null}
    </>
  );
};

export default PrivilegeLayer;
export { default as usePrivilegeLayer } from "./hooks/usePrivilegeLayer";
