import { memo } from "react";
import {
  Route,
  RouterProvider,
  createBrowserRouter,
  createRoutesFromElements,
} from "react-router-dom";
import { PageRoutes } from "./packages/constants";
import BasePage from "./packages/base-page";
import NotFoundPage from "./packages/not-found-page";
import { AppConfigProvider } from "./packages/app-config-provider";
import "./index.css";

const router = createBrowserRouter(
  createRoutesFromElements(
    <Route>
      <Route path="/" element={<BasePage />}>
        {PageRoutes.map(({ path, Component }) => {
          return (
            <Route path={path} key={path.toString()} element={<Component />} />
          );
        })}
      </Route>
      <Route path="*" element={<NotFoundPage />} />
    </Route>
  )
);

const App = () => {
  return (
    <AppConfigProvider>
      <RouterProvider router={router} />
    </AppConfigProvider>
  );
};

export default memo(App);
