import { memo } from "react";
import { iconTypes } from "../images/constants";
import { StyledButton, StyledImage } from "./style";
import { IconButtonType } from "./interface";

const IconButton = ({
  iconName,
  width,
  height,
  onClick,
  hidden,
  disabled,
}: IconButtonType) => {
  const { src, alt, backGroundColor } = iconTypes[iconName];
  return (
    <>
      <StyledButton
        width={width}
        height={height}
        onClick={onClick}
        backGroundColor={backGroundColor}
        hidden={hidden}
        disabled={disabled}
      >
        <StyledImage src={src} alt={alt} className="img" />
      </StyledButton>
    </>
  );
};

export default memo(IconButton);
