import { memo, useCallback, useState } from "react";
import { useFetch } from "../hooks";
import { primaryColors } from "../constants";
import {
  useSideBarIndex,
  useSetAuthConfigData,
  useSelectUserName,
  useMenuVisible,
} from "../app-config-provider";
import Flex from "../flex";
import Button from "../button";
import LinkedButton from "../link-button";
import {
  Nav,
  AccordionItem,
  AccordionLabel,
  AccordionPanel,
  StyledComponent,
} from "./style";

const SideBar = () => {
  const [data, setData] = useState([]);

  const setAuthConfigValues = useSetAuthConfigData();
  const user_name = useSelectUserName();
  const activeIndex = useSideBarIndex();
  const visible = useMenuVisible();
  const { lightSky, lightGray } = primaryColors;

  useFetch({
    apiId: "QUERY_SIDE_PAGES_DATA",
    fetchOnFirstRun: true,
    params: {
      user_name,
    },
    onResponse: setData,
  });

  const handleToggle = useCallback(
    (index: number) => {
      setAuthConfigValues({
        sideBlock: index,
      });
    },
    [setAuthConfigValues]
  );

  const handleMenuButton = useCallback(
    () =>
      setAuthConfigValues({
        menu_visible: !visible,
      }),
    [setAuthConfigValues, visible]
  );

  return (
    <Nav collapsed={!visible}>
      <Button
        width="90%"
        label={visible ? "menu" : undefined}
        onClick={handleMenuButton}
        margin="5%"
        icon="menu"
      />
      {Array.isArray(data) &&
        data.map((record: any, index: any) => {
          const { page_parent_id, page_parent_name, app_pages, page_icon } =
            record;
          return (
            <AccordionItem key={page_parent_id}>
              <AccordionLabel>
                <Button
                  margin="0"
                  width="100%"
                  label={visible ? page_parent_name : undefined}
                  onClick={() => handleToggle(index)}
                  borderRadius="5px"
                  icon={page_icon}
                />
              </AccordionLabel>
              <AccordionPanel visible={activeIndex === index}>
                <Flex
                  width="100%"
                  flexDirection="column"
                  gap="10px"
                  padding="0"
                >
                  {app_pages.map((page: any) => {
                    const {
                      run_in_modal,
                      page_icon,
                      page_name,
                      page_id,
                      page_link,
                    } = page;
                    return !run_in_modal ? (
                      <StyledComponent>
                        <LinkedButton
                          key={page_id}
                          label={visible ? page_name : undefined}
                          pathTo={page_link}
                          width="100%"
                          type="primary"
                          backGround={lightGray}
                          icon={page_icon}
                          color="white"
                        />
                      </StyledComponent>
                    ) : (
                      <Button
                        key={page_id}
                        label={visible ? page_name : undefined}
                        width="100%"
                        backGround={lightSky}
                        borderRadius="5px"
                        icon={page_icon}
                        color="white"
                        onClick={() => {}}
                      />
                    );
                  })}
                </Flex>
              </AccordionPanel>
            </AccordionItem>
          );
        })}
    </Nav>
  );
};

export default memo(SideBar);
export * from "./interface";
