import { memo } from "react";
import { NavLink } from "react-router-dom";
import Button from "../button";

const LinkedButton = ({ pathTo, ...props }: any) => (
  <NavLink
    to={`/${pathTo}`}
    style={{ textDecoration: "none", width: "100%", background: "none" }}
  >
    <Button {...props} />
  </NavLink>
);

export default memo(LinkedButton);
