import { useTranslateId } from "../translation-layer";
import { FloatingLabelContainer, FloatingLabelLabel } from "./style";
import { floatingLabelProps } from "./interface";

const FloatingLabel = ({
  hasContent,
  name,
  label = "",
  height,
  width,
  margin,
  children,
  hidden,
}: floatingLabelProps) => {
  const translate = useTranslateId();

  return (
    <FloatingLabelContainer
      width={width}
      height={height}
      margin={margin}
      hidden={hidden}
    >
      {children}
      <FloatingLabelLabel htmlFor={name} hasContent={hasContent}>
        {translate(label)}
      </FloatingLabelLabel>
    </FloatingLabelContainer>
  );
};
export default FloatingLabel;
