import Button from "../button";
import IconButton from "../icon-button";
import { useTranslateId } from "../translation-layer";
import { primaryColors } from "../constants";
import { BaseTitle } from "../page-title";
import {
  ModalContainer,
  ModalContent,
  ModalHeader,
  ModalBody,
  ModalFooter,
} from "./style";
import { ModalProp } from "../types";

const Modal = ({
  children,
  visible = false,
  loading,
  label,
  onClose,
  onOK,
  submitTitle = "submit",
  width = "40%",
  height = "auto",
  hideSaveButton = false,
  hideCloseButton = false,
  noFooter = false,
  zIndex,
}: ModalProp) => {
  const { danger } = primaryColors;
  const translate = useTranslateId();
  return (
    <>
      {visible && (
        <ModalContainer hidden={!visible} zIndex={zIndex}>
          <ModalContent width={width} height={height}>
            <ModalHeader>
              <BaseTitle>{translate(label)}</BaseTitle>
              <IconButton
                onClick={onClose}
                iconName="close"
                disabled={loading}
              />
            </ModalHeader>

            <ModalBody>{children}</ModalBody>

            {!noFooter && (
              <ModalFooter hidden={noFooter}>
                {!hideSaveButton && (
                  <Button
                    onClick={onOK}
                    label={submitTitle}
                    hidden={hideSaveButton}
                    width="auto"
                    disabled={loading}
                  />
                )}
                {!hideCloseButton && (
                  <Button
                    onClick={onClose}
                    label="close"
                    hidden={hideCloseButton}
                    backGround={danger}
                    width="auto"
                    disabled={loading}
                  />
                )}
              </ModalFooter>
            )}
          </ModalContent>
        </ModalContainer>
      )}
    </>
  );
};

export default Modal;
