const sortObjectByKeysAndStringify = (params?: any) => {
  let result = {};

  if (params) {
    const keys = Object.keys(params);
    if (keys.length) {
      result = keys.sort().reduce(
        (acc: any, key: string) => ({
          ...acc,
          [key]: params[key],
        }),
        {}
      );
    }
  }

  return JSON.stringify(result);
};

export default sortObjectByKeysAndStringify;
