import { useCallback } from "react";
import useTranslationLayer from "./useTranslationLayer";

const useTranslateId = () => {
  const labels = useTranslationLayer();
  const translateLabel = useCallback(
    (labelId: string) => {
      const translatedId = labels[labelId];
      return translatedId ? translatedId : labelId;
    },
    [labels]
  );

  return translateLabel;
};

export default useTranslateId;
