import { ElementType } from "react";
import { useTranslateId } from "../translation-layer"
import { BaseTitleProp } from "./interface";

const BaseTitle = ({
  children,
  tag,
  disableTranslation,
  ...otherProps
}: BaseTitleProp) => {
  const translate = useTranslateId()

  const translatedLabel = disableTranslation ? children : translate(children);

  if (tag === "fragment") {
    return <>{translatedLabel}</>;
  }

  const ProperTag = (tag || "p") as ElementType<any>;

  return <ProperTag children={translatedLabel} {...otherProps} />;
};
export default BaseTitle;
