import { useState, useCallback } from "react";
import { useSelectAuthorization } from "../app-config-provider";
import { API_ID } from "../constants";
import { useMutationProps, postFunctionProps, responseObj } from "../types";

const useMutation = ({
  apiId,
  onResponse,
  method = "POST",
}: useMutationProps) => {
  const apiLink = process.env.REACT_APP_API_LINK;
  const url = `${apiLink}/${API_ID[apiId]}`;
  const [loading, setLoading] = useState<boolean>(false);
  const authorization = useSelectAuthorization();

  const mutate = useCallback(
    async ({ data, cb }: postFunctionProps) => {
      setLoading(true);
      let dataResponse: responseObj = {};
      try {
        const requestOptions = {
          method,
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
            Authorization: `Token ${authorization}`,
          },
          body: JSON.stringify(data),
        };

        const result = await fetch(url, requestOptions);
        const jsonData = await result.json();
        const { ok, status } = result;
        if (!ok) {
          dataResponse = {
            error: jsonData,
            errorCode: status,
            response: undefined,
          };
        } else {
          dataResponse = {
            error: undefined,
            errorCode: undefined,
            response: jsonData,
          };
        }
      } catch (error) {
        console.log(error)
      } finally {
        onResponse && onResponse(dataResponse);
        cb && cb(dataResponse);
        setLoading(false);
      }
    },
    [authorization, method, onResponse, url]
  );

  return { loading, mutate };
};

export default useMutation;
