import SignInPage from "../sign-in-page";
import HomePage from "../home-page";
import UsersPage from "../users-page";
import LabelPage from "../labels-page";
import PagesPage from "../pages-page";
import PagesParentsPage from "../pages-parents-page";

const PageRoutes = [
  {
    path: "/",
    Component: SignInPage,
  },
  {
    path: "/home",
    Component: HomePage,
  },
  {
    path: "/users",
    Component: UsersPage,
  },
  {
    path: "/labels",
    Component: LabelPage,
  },
  {
    path: "pages",
    Component: PagesPage,
  },
  {
    path: "pageParents",
    Component: PagesParentsPage,
  },
];

export default PageRoutes;
