import { memo } from "react";
import { iconTypes } from "../images/constants";
import { RecordWithAnyData } from "../hooks";
import { Loading } from "../loading-overlay/style";
import { useTranslateId } from "../translation-layer"
import { StyledButton, ButtonWrapper } from "./style";
import { ButtonProp } from "./interface";

const Button = ({
  label = "",
  onClick,
  onDoubleClick,
  disabled,
  hidden,
  margin,
  width,
  height,
  padding,
  borderRadius,
  backGround,
  fontWeight,
  loading,
  border,
  fontSize,
  color,
  icon,
}: ButtonProp) => {
  const { src, alt }: RecordWithAnyData = icon ? iconTypes[icon] || {} : {};
  const translate = useTranslateId()
  return (
    <>
      {!hidden && (
        <StyledButton
          disabled={disabled || loading}
          onClick={onClick}
          onDoubleClick={onDoubleClick}
          loading={loading}
          margin={margin}
          width={width}
          height={height}
          padding={padding}
          borderRadius={borderRadius}
          backGround={backGround}
          fontWeight={fontWeight}
          fontSize={fontSize}
          border={border}
          color={color}
        >
          <ButtonWrapper>
            {icon && <img src={src} alt={alt} />}
            {loading && <Loading />}
            {translate(label)}
          </ButtonWrapper>
        </StyledButton>
      )}
    </>
  );
};

export default memo(Button);
