import { useCallback, useEffect, useState } from "react";
import { useLocation, useNavigate, Outlet } from "react-router-dom";
import TranslationLayer from "../translation-layer";
import PrivilegeLayer from "../privilege-layer";
import Flex from "../flex";
import Header from "../header";
import SideMenu from "../side-menu";
import InputText from "../input-text";
import NotFoundPage from "../not-found-page";
import {
  useLocalStorage,
  useEffectTimeOut,
  useModalVisibility,
  useFormManager,
  useMutation,
  useFetch,
} from "../hooks";
import Modal from "../modal";
import {
  useSetAuthConfigData,
  useSelectUserName,
  useUserLanguage,
} from "../app-config-provider";
import { Main, Content } from "./style";

const BasePage = () => {
  const [validateResponse, setValidateResponse] = useState<{
    have_permission?: boolean;
  }>();
  const { pathname } = useLocation();
  const user_name = useSelectUserName();
  const p_language = useUserLanguage();
  const navigate = useNavigate();
  const { visible, handleClose, handleOpen } = useModalVisibility();
  const {
    getDataFromAppLocalStorage,
    clearAllLocalStorage,
    setLocalStorageData,
  } = useLocalStorage();
  const setAuth = useSetAuthConfigData();
  const authData = getDataFromAppLocalStorage();

  const isLogInPage = pathname === "/";
  const shouldRenderChildren =
    validateResponse !== undefined && validateResponse.have_permission;

  const { mutate: validateAuth } = useMutation({
    apiId: "POST_VALIDATE_USER_LOGIN",
    method: "POST",
  });

  const { mutate, loading } = useMutation({
    apiId: "POST_SIGN_IN",
    method: "POST",
  });

  useFetch({
    apiId: "VALIDATE_USER_CAN_ACCESS_PAGE",
    onResponse: setValidateResponse,
    fetchOnFirstRun: true,
    checkForParams: true,
    params: {
      user_name,
      path_name: pathname.replace("/", ""),
    },
  });

  const {
    state: { password },
    onChange,
  } = useFormManager({
    initialValues: {
      password: "",
    },
  });

  const handleSave = useCallback(() => {
    mutate({
      data: {
        user_name,
        password,
      },
      cb: ({ response, error }) => {
        if (!error) {
          const { access_token, build_number, user_name } = response;
          const authRecord = {
            authorization: access_token,
            build_number,
            user_name,
          };
          clearAllLocalStorage();
          setLocalStorageData(authRecord);
          setAuth(authRecord);
          handleClose();
        }
      },
    });
  }, [
    clearAllLocalStorage,
    handleClose,
    mutate,
    password,
    setAuth,
    setLocalStorageData,
    user_name,
  ]);

  useEffect(() => {
    isLogInPage ? clearAllLocalStorage() : setAuth(authData);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffectTimeOut(() => {
    if (!isLogInPage && !visible) {
      validateAuth({
        data: {},
        cb: ({ error }) => {
          error && handleOpen();
        },
      });
    }
  }, 150000);

  const handleCloseModal = useCallback(() => {
    navigate("/");
    handleClose();
    clearAllLocalStorage();
  }, [clearAllLocalStorage, handleClose, navigate]);

  const normalComponent = (
    <Flex
      height="100vh"
      flexDirection="column"
      bordered
      dir={p_language === 1 ? "ltr" : "rtl"}
    >
      <Header />
      <Main>
        <SideMenu />
        <Content>
          <Outlet />
        </Content>
      </Main>
    </Flex>
  );

  return (
    <>
      {!isLogInPage && visible && (
        <Modal
          label="confirm password"
          visible={visible}
          onOK={handleSave}
          onClose={handleCloseModal}
          width="20%"
          loading={loading}
          zIndex={999}
        >
          <InputText
            value={password}
            name="password"
            onChange={onChange}
            type="password"
          />
        </Modal>
      )}
      {isLogInPage && <Outlet />}
      {validateResponse === undefined ? null : shouldRenderChildren ? (
        <TranslationLayer>
          <PrivilegeLayer>{!isLogInPage && normalComponent}</PrivilegeLayer>
        </TranslationLayer>
      ) : (
        <NotFoundPage />
      )}
    </>
  );
};

export default BasePage;
