import { useState } from "react";
import { useLocation } from "react-router-dom";
import { useUserLanguage } from "../app-config-provider"
import { useFetch } from "../hooks"
import Store from "./context/context";
import { RecordWithAnyData } from "../types";

const TranslationLayer = ({ children, componentName }: any) => {

  const [basePage, setBasePage] = useState<RecordWithAnyData>();
  const { pathname } = useLocation();
  const p_language = useUserLanguage();
  const page_name = pathname.replace('/', '');

  useFetch({
    apiId: "QUERY_GET_LINKED_LABELS",
    onResponse: setBasePage,
    fetchOnFirstRun: true,
    checkForParams: true,
    // skipQuery: !p_language && (!componentName || !page_name),
    params: {
      page_name,
      component_name: componentName,
      p_language
    }
  })

  return (
    <Store.Provider value={{ ...basePage }}>
      {children}
    </Store.Provider>
  )
}

export default TranslationLayer;
export { default as useTranslateId } from "./hooks/useTranslateId"
export { default as useTranslationLayer } from "./hooks/useTranslationLayer"